.order-id {
  white-space: pre-wrap;
  display: flex;
  flex-direction: row;
  align-items: center;
  line-height: 16px;
  height: 100%;

  &__text {
    display: -webkit-box;
    max-height: 30px;
    overflow: hidden;
    -webkit-line-clamp: 2;
    -moz-box-orient: vertical;
    -webkit-box-orient: vertical;
  }

  &__spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    transform: scale(0.9);
  }
}
